.overlay
{
    position: fixed;
    overflow: hidden;
    pointer-events: none;
    width: 100%;
    height: 100vh;
    top: 0;
    left: 0;
    z-index: 9;
    opacity: 0;
    transition: all .2s ease;
    background: #000000d9;

    &.active
    {
        overflow: visible;
        pointer-events: visible;
        opacity: 1;
    }
}

.app-modal
{
    text-align: center;
    position: fixed;
    overflow: hidden;
    pointer-events: none;
    max-width: 100%;
    width: 450px;
    height: auto;
    top: -50%;
    left: 50%;
    opacity: 0;
    border-radius: 10px;
    transform: translate(-50%, -50%);
    z-index: 10;
    transition: all .4s ease;
    background: #fff;
    
    &-content
    {
        position: relative;
        width: 100%;
        padding: 40px;
        height: auto;

        h2
        {
            font-size: 2.4em;
        }

        img
        {
            margin-bottom: 15px;
        }

        .modal-close
        {
            font-size: 2em;
            cursor: pointer;
            position: absolute;
            top: 15px;
            right: 15px;

            &.posr
            {
                font-size: 1.4em;
                position: static;
                top: 0;
                right: 0;
            }
        }
    }

    &.active
    {
        overflow: visible;
        pointer-events: visible;
        opacity: 1;
        top: 50%;
    }
}