.sec-tle
{
    text-align: center;
    font-weight: 700;
    font-size: 3.6em;
    margin-bottom: 50px;

    &.for-btns
    {
        display: flex;
        align-items: center;
        justify-content: space-between;

        .control
        {
            display: flex;

            i
            {
                font-size: 0.8em;
                transition: .3s ease;
            }

            div:hover i
            {
                color: $orange;
            }
        }
    }

    @include mq('phablet', max)
    {
        font-size: 2.8em;
        margin-bottom: 30px;
    }
}