.procces
{
    .col-md-6
    {
        text-align: center;
        margin-bottom: 20px;
        border-radius: 8px;
        
        .box
        {
            width: 100%;
            height: auto;
            padding: 20px;
            transition: box-shadow .4s ease;
            box-shadow: 0px 15px 30px #05080908;
            
            &:hover
            {
                box-shadow: 0px 15px 30px #0508091f;
            }
        }

        img
        {
            object-fit: contain;
            max-width: 100%;
            height: 60px;
            margin: 30px 0;
        }

        h4
        {
            font-weight: bold;
            font-size: 1.8em;
            margin-bottom: 10px;
        }

        p
        {
            font-size: 1.4em;
            margin: 0;
        }

        @include mq('phablet', max)
        {
            h4
            {
                font-size: 1.6em;
            }
        }
    }
}