.app-btn
{
    font-weight: bold;
    font-size: 1.4em;
    text-transform: uppercase;
    position: relative;
    display: block;
    width: auto;
    height: auto;
    padding: 20px 35px;
    margin-right: auto;
    border-radius: 28px;
    outline: 0!important;
    border: 3px solid #7629936b;
    // box-shadow: 0px 12px 50px rgba(5, 8, 9, 0.12);
    color: #fff;
    background-image: linear-gradient(180deg, rgba(251,163,69,1) 0%, rgba(207,87,1,1) 100%);

    @include mq('phablet', max)
    {
        font-size: 1.2em;
    }
}