.swiper-container
{
    width: 100%;
    height: auto;
}

.swiper-1
{
    .swiper-slide
    {
        position: relative;
    }

    h2
    {
        font-weight: 700;
        font-size: 3em;
        color: $orange;
        margin-bottom: 25px;
    }
    
    p
    {
        font-size: 1.6em;
        color: #fff;
        margin-bottom: 60px;
    }

    .control
    {
        position: relative;
        display: flex;
        flex-direction: column;
        width: auto;
        height: auto;

        div
        {
            display: flex;
            margin-bottom: 20px;
            
            i
            {
                font-size: 2em;
                color: #fff;
                margin-right: 15px;
            }
        }

        .swiper-pagination-1
        {
            color: #fff;
            font-size: 2.4em;
            width: auto;
            margin: 0;

            .swiper-pagination-current
            {
                font-weight: 700;
            }

            .swiper-pagination-current::before,
            .swiper-pagination-total::before
            {
                content: '0';
            }
        }

        .swiper-next-1.swiper-button-disabled,
        .swiper-prev-1.swiper-button-disabled
        {
            opacity: .5;
        }
    }

    .img-box
    {
        position: absolute;
        width: 50%;
        height: 100%;
        top: 0;
        right: 0;
        background: #f8f8f8;

        img
        {
            position: relative;
            object-fit: contain;
            object-position: left center;
            max-width: 100%;
            width: 100%;
            height: 100%;
            padding-left: 15px;
        }
    }

    @include mq('tablet', max)
    {
        text-align: center;

        .img-box
        {
            position: static;
            width: 100%;
            height: 300px;
        }

        .control
        {
            align-items: center;
        }
    }

    @include mq('phablet', max)
    {
        h2
        {
            font-size: 2.4em;
        }

        p
        {
            font-size: 1.4em;
        }
    }
}

.swiper-2
{
    img
    {
        object-fit: cover;
        object-position: center;
        max-width: 100%;
        width: 100%;
        height: 350px;
        margin-bottom: 20px;
    }

    h4
    {
        font-weight: bold;
        font-size: 2em;
        margin-bottom: 10px;
    }

    p
    {
        font-size: 1.4em;
        margin-bottom: 0;
    }

    @include mq('tablet', max)
    {
        text-align: center;

        h4
        {
            font-size: 1.8em;
        }
    }
}

.swiper-3
{
    .swiper-slide
    {
        text-align: center;
        color: #fff;
    }
    
    img
    {
        object-fit: cover;
        object-position: center center;
        width: 80px;
        height: 80px;
        margin-bottom: 10px;
        border-radius: 50%;
    }

    h4
    {
        font-weight: bold;
        font-size: 2em;
        margin-bottom: 5px;
    }

    span
    {
        font-size: 1.6em;
        display: block;
        margin-bottom: 20px;
    }

    p
    {
        font-size: 1.8em;
    }

    @include mq('phablet', max)
    {
        h4
        {
            font-size: 1.8em;
        }

        p
        {
            font-size: 1.4em;
        }
    }
}