main
{
    position: relative;
    width: 100%;
    height: auto;

    section
    {
        position: relative;
        width: 100%;
        height: auto;

        &.with-p
        {
            padding-top: 100px;

            @include mq('tablet', max)
            {
                padding-top: 50px;
            }
        }

        &.with-py
        {
            padding: 100px 0;

            @include mq('tablet', max)
            {
                padding: 50px 0;
            }
        }
    }
    
    .intro
    {
        position: relative;
        width: 100%;
        min-height: 100vh;
        height: auto;
        background: url('../img/intro-bg.jpg') no-repeat;
        background-size: cover;

        &-item
        {
            position: absolute;
            width: 100%;
            height: auto;
            top: 50%;
            left: 0;
            transform: translateY(-50%);
            color: #fff;

            .col-md-6
            {
                display: flex;
                flex-direction: column;
                justify-content: center;
            }

            h1
            {
                font-weight: 700;
                font-size: 4em;
                margin-bottom: 35px;
            }

            p
            {
                font-size: 1.6em;
                margin-bottom: 70px;
            }
            
            img
            {
                width: 100%;
            }

            @include mq('tablet', max)
            {
                text-align: center;

                button
                {
                    margin: 0 auto;
                }

                img
                {
                    display: none;
                }
            }

            @include mq('phablet', max)
            {
                h1
                {
                    font-size: 2.8em;
                    margin-bottom: 20px;
                }

                p
                {
                    font-size: 1.6em;
                    margin-bottom: 50px;
                }
            }
        }
    }

    .about-company
    {
        .col-md-6
        {
            position: static;
            padding: 100px 0;
            padding-left: 70px;
        }

        img.main
        {
            position: absolute;
            object-fit: cover;
            width: 50%;
            height: 100%;
            top: 0;
            left: 0;
        }

        img:not(.main)
        {
            max-width: 100%;
            margin-bottom: 15px;
        }

        h4
        {
            font-weight: 600;
            font-size: 1.6em;
            margin-bottom: 45px;
        }

        p
        {
            font-size: 1.4em;
            margin-bottom: 20px;
        }

        @include mq('tablet', max)
        {
            padding-top: 50px;

            .col-md-6
            {
                padding: 0 15px;

                &:first-child
                {
                    display: none;
                }
            }

            h4
            {
                text-align: center;
                margin-bottom: 30px;
            }

            p
            {
                margin-bottom: 15px;
            }
        }
    }

    .equipment
    {
        .col-md-6
        {
            position: static;
            padding: 80px 0;
        }

        img.bg
        {
            position: absolute;
            object-fit: cover;
            width: 50%;
            height: 100%;
            top: 0;
            left: 0;
            z-index: -1;
        }

        div.main
        {
            padding: 80px 0;
        }

        @include mq('tablet', max)
        {
            .container
            {
                max-width: 100%;
                width: 100%;
                margin: 0;
            }

            .col-md-6
            {
                position: relative;
                padding: 0;
            }

            .col-md-6.main
            {
                padding: 50px 15px;
            }

            img.bg
            {
                width: 100%;
            }
        }
    }

    .comment
    {
        overflow: hidden;
        background: url(../img/comment-bg.jpg) no-repeat;
        background-size: cover;
        background-position: center;

        .container
        {
            position: relative;
        }

        .control
        {
            font-size: 2.4em;
            position: absolute;
            display: flex;
            justify-content: space-between;
            width: 100%;
            height: 1px;
            top: 50%;
            left: 0;
            color: #fff;

            .swiper-next-1.swiper-button-disabled,
            .swiper-prev-1.swiper-button-disabled
            {
                opacity: .5;
            }
        }

        &::before
        {
            position: absolute;
            content: '”';
            width: auto;
            height: auto;
            top: -420px;
            right: 100px;
            font-size: 90em;
            color: #fff;
        }

        @include mq('desktop-small', max)
        {
            &::before
            {
                right: 0;
            }
        }

        @include mq('tablet', max)
        {
            &::before
            {
                display: none;
            }
        }

        @include mq('phablet', max)
        {
            .control
            {
                width: calc(100% - 30px);
                left: 15px;
                top: 20%;
            }
        }
    }

    .cooperation
    {
        .col-md-6
        {
            position: static;
            padding: 100px 0;
            padding-left: 70px;
            padding-bottom: 0;
        }

        img.main
        {
            position: absolute;
            object-fit: cover;
            object-position: right top;
            width: 50%;
            height: 100%;
            top: 0;
            left: 0;
            padding-top: 100px;
        }

        h4
        {
            font-weight: bold;
            font-size: 2.4em;
            margin-bottom: 10px;
            color: $orange;
        }

        p
        {
            font-size: 1.4em;
            margin-bottom: 30px;
        }

        @include mq('tablet', max)
        {
            text-align: center;
            padding-top: 50px;

            .col-md-6
            {
                padding: 0 15px;

                &:first-child
                {
                    display: none;
                }
            }

            h4
            {
                font-size: 1.8em;
            }
        }
    }

    .footer
    {
        background: url(../img/footer.jpg) no-repeat;
        background-size: cover;
        background-position: center;
        color: #fff;

        h4
        {
            font-weight: bold;
            font-size: 2em;
        }

        p
        {
            font-size: 1.6em;
        }

        @include mq('tablet', max)
        {
            text-align: center;
        }

        @include mq('phablet', max)
        {
            h4
            {
                font-size: 1.8em;
            }

            p
            {
                font-size: 1.4em;
            }
        }
    }
}

footer
{
    position: relative;
    display: flex;
    align-items: center;
    width: 100%;
    height: 80px;
    padding: 0 15px;
    
    .container
    {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    
    p
    {
        font-size: 1.4em;   
        margin: 0;
    }

    @include mq('tablet', max)
    {
        align-items: center;
        text-align: center;

        .container
        {
            flex-direction: column;
        }
    }
}