header
{
    position: fixed;
    width: 100%;
    height: auto;
    top: 0;
    left: 0;
    z-index: 5;
    transition: all .3s ease;

    nav
    {
        position: relative;
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        height: auto;
        padding: 30px 0;
        transition: all .3s ease;

        .brand
        {
            z-index: 10;
        }

        ul
        {
            display: flex;
            padding: 10px 0;
            margin: 0;


            li
            {
                list-style-type: none;

                a
                {
                    font-weight: 500;
                    font-size: 1.4em;
                    position: relative;
                    display: block;
                    margin: 0 .625rem;
                    color: #fff!important;

                    &::before
                    {
                        position: absolute;
                        content: '';
                        width: 0%;
                        height: 2px;
                        bottom: -10px;
                        left: 50%;
                        border-radius: 5px;
                        transform: translateX(-50%);
                        transition: all .2s ease;
                        background: #fff;
                    }

                    &:hover
                    {
                        &::before
                        {
                            width: 100%;
                        }
                    }
                }
            }
        }

        .callback
        {
            text-align: right;
            display: flex;
            flex-direction: column;

            a
            {
                font-weight: 600;
                font-size: 1.4em;
                color: #fff!important;
            }
        }

        .mob-nav
        {
            position: fixed;
            width: 100%;
            height: 0;
            top: 0;
            left: 0;

            & > div
            {
                position: relative;
                width: 100%;
                height: 0;

                & > div
                {
                    position: absolute;
                    width: 100vw;
                    min-height: 100vh;
                    height: auto;
                    top: 0;
                    left: 0;
                    transition: all .4s ease;
                    transform: translateY(-100%);

                    &.active
                    {
                        transform: translateY(0);
                        
                        &:nth-child(2)
                        {
                            transition-delay: .1s;
                        }
                        
                        &:nth-child(3)
                        {
                            transition-delay: .2s;
                        }
                        
                        &:nth-child(4)
                        {
                            transition-delay: .3s;
                        }
                    }
                    
                    &:nth-child(1)
                    {
                        background: #1fdddf;
                        transition-delay: .3s;
                    }
                    
                    &:nth-child(2)
                    {
                        background: #270ab0;
                        transition-delay: .2s;
                    }
                    
                    &:nth-child(3)
                    {
                        background: #e2771e;
                        transition-delay: .1s;
                    }
                    
                    &:nth-child(4)
                    {
                        transition-delay: 0;
                        background: #000;
                    }
                }

                .main
                {
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    ul
                    {
                        display: flex;
                        flex-direction: column;
                        padding: 100px 0;
                        margin: 0;

                        li
                        {
                            text-align: center;
                            list-style-type: none;
                            margin-bottom: 10px;

                            a
                            {
                                font-weight: 700;
                                font-size: 3.2em;
                                position: relative;
                                display: block;
                                margin: 0 .625rem;
                                color: #fff!important;

                                &::before
                                {
                                    display: none;
                                }
                            }
                        }

                        @include mq('phablet', max)
                        {
                            li a
                            {
                                font-size: 2.4em;
                            }
                        }
                    }
                }
            }
        }

        @include mq('desktop-small', max)
        {
            ul li a
            {
                font-size: 1.2em;
            }

            .callback a
            {
                font-weight: 500;
                font-size: 1.2em;
            }
        }

        @include mq('tablet-mid', max)
        {
            padding: 10px 0;

            ul
            {
                display: none;
            }
            
            .callback
            {
                display: none;
            }
        }
    }

    &.scrolled
    {
        background: #000000a1;

        nav
        {
            padding: 15px 0;
        }
    }
}